@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Libre+Baskerville:wght@700&display=swap);

body{

    margin: 0;

    font-family: Roboto;

    --title: 'Libre Baskerville', serif;

    background-color: #08AEEA;
    background-image: linear-gradient(0deg, #08AEEA 0%, #2AF598 100%);

}

*,
*::before,
*::after{

    box-sizing: border-box;

}

#root{

    width: 100vw;
    height: 100vh;

    display: grid;

    grid-template-rows: -webkit-min-content auto ;

    grid-template-rows: min-content auto ;

}

p{

    width: -webkit-max-content;

    width: -moz-max-content;

    width: max-content;

}

input{

    outline: none;

    border: none;

    border-radius: 50px;

    box-shadow: -3px 3px 3px -2px rgb(0, 0, 0);

}

h2{

    font-family: var(--title);

}

button{

    outline: none;

    border: none;

    border-radius: 2px;

    cursor: pointer;

    box-shadow: -3px 3px 3px -2px rgb(0, 0, 0);

}

p, h2{

    margin: 0;

}

header{

    width: 100%;

    height: 100px;

    display: grid;

    grid-template-columns: repeat(4 ,1fr);

    box-sizing: border-box;

    grid-gap: 10px;

    gap: 10px;

    padding: 10px;

    margin-bottom: 60px;

}

header > *{

    font-size: 1.7em;

}

.product-manager-container{

    display: grid;

    width: 100%;

    height: 100%;

    grid-template-rows: -webkit-min-content 0.9fr;

    grid-template-rows: min-content 0.9fr;

    overflow: hidden;

    padding-left: 20px;

    padding-right: 20px;

    grid-gap: 30px;

    gap: 30px;

}

.main-control{

    height: 70px;

    display: grid;

    grid-template-columns: 0.7fr 0.6fr ;

    grid-gap: 20px;

    gap: 20px;

}

.main-control *{

    font-size: 1.5em;

}

.main-control .input-container{

    width: 100%;
    height: 100%;

    position: relative;

    display: flex;

    align-items: center;

}

.main-control .input-container .lupa{

    position: absolute;

    background-size: 100% 100%;

    background-repeat: no-repeat;

    width: 25px;

    height: 25px;

    background-image: url(../../static/media/lupa.38b7fe7e.svg);

    left: 0.8em;

}

.main-control input{

    width: 100%;
    height: 100%;

    border-radius: 50px;

    text-indent: 2em;

    box-shadow: -2px 2px 3px -2px rgb(0, 0, 0);

}

.main-control input::-webkit-input-placeholder{

    font-size: 1.8rem;

}

.main-control input:-ms-input-placeholder{

    font-size: 1.8rem;

}

.main-control input::-ms-input-placeholder{

    font-size: 1.8rem;

}

.main-control input::placeholder{

    font-size: 1.8rem;

}

.main-control button{

    position: relative;

    border-radius: 50px;

    box-shadow: -2px 2px 3px -2px rgb(0, 0, 0);

}

.main-control button .plus{

    position: absolute;

    background-image: url(../../static/media/plus.347a0449.svg);

    background-size: 100% 100%;

    background-repeat: no-repeat;

    width: 30px;

    height: 30px;

    left: 1em;

}

main{

    padding-left: 10px;

    padding-right: 10px;

    width: 100%;

    height: 100%;

    display: flex;

    flex-direction: column;

    align-items: center;

    overflow-x: hidden;

}

main .order-bar{

    width: 100%;

    display: grid;

    grid-template-columns: repeat(6, 1fr);

    justify-items: center;

    margin-bottom: 20px;

    font-weight: bold;

    font-size: 1.3em;

    font-style: italic;

}

main .order-bar p{

    cursor: pointer;

    -webkit-user-select: none;

        -ms-user-select: none;

            user-select: none;

}

main .list-items{

    height: 100%;

    width: 100%;

    overflow-y: scroll;

    padding-right: 5px;
    padding-left: 5px;

}

main .product-item{

    display: grid;

    border-radius: 3px;

    grid-template-columns: repeat(6, 1fr);

    align-items: center;

    justify-items: center;

    grid-gap: 20px;

    gap: 20px;

    width: 100%;

    background-color: rgba(0, 0, 0, 0.5);

    padding: 5px;

    padding-left: 20px;

    box-sizing: border-box;

    margin-bottom: 20px;

    color: white;

    height: 70px;

    font-size: 1em;

    position: relative;

}

main .product-item:focus{

    outline: none;

    background-color: rgba(0, 0, 0, 0.7);

}

main .product-item .modal{

    position: absolute;

    width: 100%;

    height: 100%;

    background-color: rgba(0, 0, 0, 0.8);

    -webkit-backdrop-filter: blur(5px);

            backdrop-filter: blur(5px);

    display: flex;

    justify-content: space-around;

    z-index: 100;

    padding: 10px;

    -webkit-user-select: none;

        -ms-user-select: none;

            user-select: none;

}

main .product-item .modal > button{

    width: 300px;

    color: white;

    background-color: red;

    text-shadow: 0 0 3px black;

}

main .product-item .modal > button:first-child{

    background-color: green;

}

main .product-item .delete{

    display: flex;

    align-items: center;

    border-right: 10px rgb(255, 0, 0) solid;
    border-left: 10px transparent solid;

}

main .product-item .delete .trash{

    background-image: url(../../static/media/trash.19bf067b.svg);

    background-size: 100% 100%;

    width: 25px;

    height: 25px;

    margin-right: 8px;

}

main .product-item .code{

    font-style: italic;

}


main .product-item button{

    font-size: 1.3em;

    width: 100%;
    height: 100%;

    font-weight: bold;

}

main .product-item .edit-data{

    width: 100%;

    height: 100%;

    display: grid;

    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);

    grid-gap: 5px;

    gap: 5px;

    font-size: 13px;

}

main .product-item .edit-data .cancel{

    grid-column: 1/3;

}

main .product-item input{

    font-size: inherit;

    width: 100%;

    font-weight: bold;

    padding: 5px;

    text-indent: 5px;

}

main .product-item input:disabled{

    background-color: transparent;

    color: white;

    box-shadow: none;

}

main .product-item input:last-child{

    margin-bottom: 0;

}

.facture-manager-container{

    padding: 10px;

    position: relative;

}

.facture-manager-container .reset{

    position: absolute;

    background-image: url(../../static/media/reset.71e63a61.svg);

    width: 50px;
    height: 50px;

    background-size: 100% 100%;

    background-color: #0056ff;

    top: 10px;

    right: 10px;

    border-radius: 4px;

    cursor: pointer;

}

.calculator{

    display: grid;

    grid-template-columns: repeat(3, 1fr);

    grid-template-rows: 100%;

    height: 400px;

    width: 100%;

    margin-bottom: 40px;

}

.calculator .main-input{

    display: grid;

    justify-items: center;

    align-items: center;

    grid-gap: 20px;

    gap: 20px;


}

.calculator .main-input input{

    border: none;

    width: 100%;

    height: 100%;

    font-size: 2em;

    text-indent: 25px;

}

.calculator .main-input .result{

    position: relative;

    width: 100%;

    height: 100%;

    display: flex;

    justify-content: center;

    align-items: center;

    flex-direction: column;

}

.calculator .main-input .result .modal{

    position: absolute;

    width: 100%;

    height: 100%;

    -webkit-backdrop-filter: blur(10px);

            backdrop-filter: blur(10px);

    background-color: rgba(0, 0, 0, 0.8);

    border-radius: 3px;

    font-size: 6px;

    display: flex;

    flex-direction: column;

    justify-content: center;

    align-items: center;

    color: red;

    font-weight: bold;

}

.calculator .main-input .result .modal .cross{

    background-image: url(../../static/media/cross.15d52503.svg);
    
    background-size: 60% 60%;

    background-position: center;

    background-repeat: no-repeat;

    width: 40px;

    height: 40px;

    position: absolute;

    top: 10px;
    right: 10px;

    background-color: red;

    border-radius: 50%;

}

.calculator .main-input .result *{

    font-size: 3em;

    margin: 0;

    font-family: 'Libre Baskerville', serif;

}

.calculator .main-input button{

    width: 100%;

    height: 100%;


    border: none;

    font-size: 3em;

}

.calculator .product{

    display: flex;

    flex-direction: column;

    align-items: center;

    justify-content: space-evenly;

    font-size: 2em;

    font-weight: bold;

}

.calculator .product .container{

    width: 100%;

    display: grid;

    grid-template-rows: 0.5fr 0.5fr;

    justify-items: center;

    align-items: center;

}

.calculator .product .container .value{

    height: 1em;

}

.history{

    height: 100%;

    width: 100%;

    display: grid;

    grid-template-rows: 0.2fr 0.8fr;

    justify-items: center;

    align-items: center;

}

.history .view{

    display: flex;

    align-items: center;

    flex-direction: column;

    width: 100%;

    height: 100%;

    overflow-y: scroll;

    padding-top: 10px;

}

.history .view .bill-history-Item{

    padding: 10px;

    background-color: white;

    margin-bottom: 10px;

    width: 95%;

}

.history .view .bill-history-Item p{

    margin-top: 3px;

}

.history .view .bill-history-Item button{

    margin-top: 10px;

    padding: 5px;

    border: none;

    color: white;

    background-color: black;

}

.pay{

    width: 100%;

    display: grid;

    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-gap: 20px;
    gap: 20px;

    align-items: center;

}

.pay > input{

    border: none;

    font-size: 2em;

    text-indent: 20px;

    width: 100%;

    height: 100%;

    padding: 10px;

}

.pay .card{

    grid-column: 1/4;

}

.pay .name{

    grid-column: 1/4;
    grid-row: 2;

}

.pay .facture{

    grid-column: span 3;
    grid-row: span 2;

    font-size: 3em;

    height: 80%;

}

.pay .total{

    font-family: var(--title);

    grid-column: 7/13;

    display: flex;

    grid-row: span 2;

    justify-content: space-evenly;
}

.pay .total .title{

    font-size: 1.8em

}

.bill-print{

    position: absolute;

    width: 100%;

    height: 100%;

    background-color: white;

    top: 0;

    left: 0;

    z-index: 100;

    display: flex;

    justify-content: center;

    align-items: center;

}

.bill-print .cross{

    position: absolute;

    top: 50px;

    right: 50px;

    background-image: url(../../static/media/cross.15d52503.svg);

    background-size: 80% 80%;

    background-color: red;

    background-repeat: no-repeat;

    background-position: center;

    height: 80px;

    width: 80px;

    border: none;

    border-radius: 3px;

    cursor: pointer;

}

.bill-print .check-mark{

    position: absolute;

    top: 50px;

    left: 50px;

    background-image: url(../../static/media/check-mark.fe0b62ec.svg);

    background-size: 80% 80%;

    background-color: green;

    background-repeat: no-repeat;

    background-position: center;

    height: 80px;

    width: 80px;

    border: none;

    border-radius: 3px;

    cursor: pointer;

}

.bill-print > div{

    padding: 10px;

    width: 400px;

    height: 600px;

    border: 2px black solid;

    display: grid;

    grid-template-rows: 0.3fr 0.2fr 0.2fr auto -webkit-min-content;

    grid-template-rows: 0.3fr 0.2fr 0.2fr auto min-content;

    grid-gap: 10px;

    gap: 10px;

    align-items: center;

}

.bill-print > div > *{

    width: 100%;

    height: 100%;

}

.bill-print .data-company{

    display: grid;

    grid-template-columns: -webkit-min-content -webkit-max-content;

    grid-template-columns: min-content max-content;

    grid-template-rows: -webkit-min-content;

    grid-template-rows: min-content;

}

.bill-print .data-company .logo{

    background-image: url(../../static/media/logo.f8fb0362.png);

    background-size: 100% 100%;

    width: 100px;

    height: 100px;

    margin-right: 10px;

}

.bill-print .data-company h3{

    margin: 0;

    grid-column: 2;

}

.bill-print .data-company .container{

    display: flex;

    align-items: center;

    justify-content: center;

    flex-direction: column;

    width: 100%;

    height: 100%;

}

.bill-print .data-bill{

    display: grid;

    grid-template-columns: repeat(2 1fr);
    grid-template-rows: repeat(2 1fr);

}

.bill-print .data-bill h4{

    grid-column: 1/3;

    width: 100%;

    text-align: center;

}

.bill-print .data-bill .keys{

    grid-column: 1;

}

.bill-print .data-bill .values{

    grid-row: 2;

    grid-column: 2;

    display: flex;

    flex-direction: column;

    align-items: flex-end;

}

.bill-print .products .item{

    display: flex;

    justify-content: space-between;

}

.bill-print .result .container{

    width: 100%;

    display: flex;

    justify-content: space-between;

}

.bills-manager-container{

    overflow-y: scroll;

    padding-left: 40px;

    padding-right: 40px;

    display: flex;

    flex-direction: column;

    align-items: center;

}

.bills-manager-container .search{

    width: 60%;

    height: 80px;

    text-indent: 1.5em;

    font-size: 2em;

    margin-bottom: 40px;

}


.bills-manager-container .bill{

    display: grid;

    width: 100%;

    grid-template-rows: repeat(2, 1fr) -webkit-min-content;

    grid-template-rows: repeat(2, 1fr) min-content;

    background-color: rgb(255, 255, 255);

    padding-top: 20px;
    padding-bottom: 20px;

    border-radius: 5px;

    margin-bottom: 20px;

    grid-gap: 15px;

    gap: 15px;

    position: relative;

}

.bills-manager-container .bill .print{

    position: absolute;

    top: 10px;
    right: 10px;

    width: 50px;
    height: 50px;

    background-image: url(../../static/media/print.a42f373f.svg);

    border-radius: 4px;

    background-size: 100% 100%;

    cursor: pointer;

    background-color: black;

}

.bills-manager-container .bill .top{

    display: flex;

    flex-direction: column;

    justify-content: center;

    align-items: center;

    font-size: 2em;

    font-weight: bold;

    border-bottom: 1px black solid;

    padding-left: 100px;
    padding-right: 100px;

}

.bills-manager-container .bill .top > *:first-child{

    margin-bottom: 5px;

}

.bills-manager-container .bill .top > *{

    margin-bottom: 10px;

}

.bills-manager-container .bill .middle h3{

    margin: 0;

    display: flex;

    justify-content: center;

    font-size: 2em;

    margin-bottom: 15px;

}

.bills-manager-container .bill .middle .product{

    display: flex;

    width: 100%;

    justify-content: space-evenly;

    font-size: 1.5em;

}

.bills-manager-container .bill .bottom{

    display: flex;

    justify-content: space-evenly;

    font-size: 1.5em;

    background-color: black;

    color: rgb(255, 255, 255);

    padding: 10px;

}
.login{

    display: flex;
    flex-direction: column;
    font-size: 3em;
    align-items: center;

}

.login > *{

    padding: 20px;

    margin-bottom: 30px;

    width: 60%;

}

.login input{

    font-size: inherit;

}

.login button{

    font-size: inherit;

}

.login > *:last-child{

    margin: 0;

}
